body{
    
    font-family:sans-serif;
	position: relative;
	background: white;
}
#img {
    padding-top: 2px;
}
.head-section{
	background: #F3F3F3;
    background-color:snow;
	text-align: center;
	padding: 15px 0px;
	border-bottom: 1px solid #CECECE;
}
.head-section h2{
    font-size:32px;
    font-weight: 600px;
	margin: 20px;
	color: #565656;
    color:brown;
}
.body-section{
	padding:30px 30px 30px 30px;
	overflow: hidden;
}
.body-section .form-input{
	width: 100%;
	padding: 20px 0px;
}
.body-section .form-input input[type='email'] input[type='password']{
	width: calc(100% - 30px);
	border: 2px solid #D3D3D3;
	border-radius: 2px;
	padding:15px 15px;
	-webkit-box-shadow: 0px 0px 0px 5px rgb(239,244,247);
	        box-shadow: 0px 0px 0px 5px rgb(239,244,247);
}
.body-section .form-input input[type='checkbox']{
	float: left;
}
.body-section label{
	color: #565656;
	padding: 1px 5px;
	float: left;
}
.body-section .btn-submit{
	float: right;
	background: #DEEDF4;
	border:5px solid #B5CBCD;
	color: #56778E;
	font-weight: bolder;
    font-size: 20px;
	padding:9px 20px;
	border-radius:10px;
}



